/* src/administrator_layout/AdminLayout.css */ 

.layout {
    display: flex;
    height: 100vh;
    overflow-y: auto;
  }
  
  .sidebar {
    position: -webkit-sticky; /* For Safari */
    position: sticky;
    top: 0; /* Stick to the top of the container */
    width: 250px;
    background-color: purple;
    color: white;
    padding: 20px;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  }
  
  .sidebar h1 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .sidebar nav ul {
    list-style-type: none;
    padding: 0;
  }
  
  .sidebar nav ul li {
    margin-bottom: 15px;
  }
  
  .sidebar nav ul li a {
    color: white;
    text-decoration: none;
    font-size: 18px;
  }
  
  .sidebar nav ul li a:hover {
    text-decoration: underline;
  }
  
  .content {
    flex: 1;
    padding: 20px;
  }
  
    .logout-button {
      margin-top: 20px;
      padding: 10px;
      background-color: #ff4d4d;
      color: white;
      border: none;
      cursor: pointer;
      border-radius: 4px;
      text-align: center;
    }
  
    .logout-button:hover {
      background-color: #ff3333;
    }
.toggle-sidebar {
  background: transparent;
  border: none;
  color: #ff4d4d;
  font-size: 24px;
  cursor: pointer;
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 1001;
  /* Ensure it stays on top of everything */
}

/* Media Query for Small Screens */
@media (max-width: 768px) {
  .sidebar {
    position: fixed;
    height: 100%;
    z-index: 1000;
  }

  .layout {
    flex-direction: column;
  }

  .content {
    margin-left: 0;
    margin-top: 60px;
    /* Adjust based on your header size */
  }
}