/* src/production_layout/production_list/ProductionList.css */

/* Main container with scrolling */
.production-list-container {
  padding: 20px;
  height: calc(100vh - 40px); /* Adjust height as needed */
  overflow-y: auto; /* Allow vertical scrolling if content exceeds viewport */
  box-sizing: border-box; /* Include padding in height calculation */
}

/* Filters section */
.filters {
  margin-bottom: 20px;
}

.filters input,
.filters select {
  margin-right: 10px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Button styles */
button {
  padding: 6px 12px; /* Reduce padding for a more compact button */
  margin: 2px; /* Reduce margin to decrease space between buttons */
  background-color: #007bff; /* Blue background color */
  border: 1px solid #007bff; /* Blue border color */
  border-radius: 5px;
  color: white; /* White text color */
  cursor: pointer;
  font-size: 12px; /* Adjust font size */
}

button:hover {
  background-color: #0056b3; /* Darker blue on hover */
  border-color: #0056b3; /* Matching border color on hover */
}

/* Table styling */
table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f4f4f4;
}

/* Pagination styling */
.pagination {
  margin-top: 20px;
  text-align: center;
}

/* List styling */
.vertical-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.vertical-list li {
  padding: 4px 0;
}

/* Popup overlay and content */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background: white;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
}

/* Add production button */
.add-production-button {
  padding: 10px 20px;
  margin: 0 10px 20px; /* Add bottom margin to space out from the table */
  background-color: #007bff; /* Blue background color */
  border: 1px solid #007bff; /* Blue border color */
  border-radius: 5px;
  color: white; /* White text color */
  cursor: pointer;
  font-size: 14px;
}

.add-production-button:hover {
  background-color: #0056b3; /* Darker blue on hover */
  border-color: #0056b3; /* Matching border color on hover */
}
